import React, { useContext } from 'react'
import { Navbar, Nav, Container, Button, Image } from 'react-bootstrap'
// import { LinkContainer } from 'react-router-bootstrap'
import ThemeContext from './ThemeContext'
import logo_lite from '../logo_lite1.png'
import logo_dark from '../logo_dark1.png'

const Header = ({name, social}) => {
	const themeContext = useContext(ThemeContext)

  	return (
    	<header id="header">
			{/* For Logo - Mohit(#6c93b9), M(#ffbd4a) */}
			<Navbar fixed="top" bg="none" expand="md" collapseOnSelect="true" className="py-0 m-0">
				<Container fluid>
					{/* <LinkContainer to='/'> */}
					<a href="#">
						<Navbar.Brand>
							<Image id="main-logo" src={themeContext.theme === 'light' ? logo_lite : logo_dark} />
						</Navbar.Brand>
					</a>
					{/* </LinkContainer> */}
					<Navbar.Toggle aria-controls="basic-navbar-nav">
						<i className="fas fa-bars"></i>
					</Navbar.Toggle>
					<Navbar.Collapse id="basic-navbar-nav">
						
						<Nav className="ml-auto align-items-center">
							{/* <LinkContainer to='/resume'>
								<Nav.Link>Resume</Nav.Link>
							</LinkContainer> */}
							
							{/* <LinkContainer to=''>
								<Nav.Link>About Me</Nav.Link>
							</LinkContainer> */}
							<a className="mx-2" href="#experience">Experience</a>
							<a className="mx-2" href="#education">Education</a>
							<a className="mx-2" href="#projects">Projects</a>

							<><a className="mx-2" href={social[0]} target="_blank" rel="noreferrer"><i className="fab fa-linkedin fa-2x"></i></a></>
                    		<><a className="mx-2" href={social[1]} target="_blank" rel="noreferrer"><i className="fab fa-github fa-2x"></i></a></>
                    		<><a className="mx-2" href={social[2]} target="_blank" rel="noreferrer"><i className="fab fa-facebook fa-2x"></i></a></>

							{/* <h5 className="mx-2" onClick={themeContext.toggleTheme}> */}
							{themeContext.theme === 'light' ? 
								<i className="fas fa-moon fa-2x mx-2" onClick={themeContext.toggleTheme}></i>
								: 
								<i className="fas fa-sun fa-2x mx-2" onClick={themeContext.toggleTheme}></i>
							}
							{/* </h5> */}
						</Nav>
					</Navbar.Collapse>
				</Container>
			</Navbar>
		</header>
  )
}

export default Header
