import React, { useState } from 'react'
import { Button, Col, Container, Image, Row } from 'react-bootstrap';
import { Document, Page, pdfjs } from 'react-pdf';
import 'react-pdf/dist/esm/Page/AnnotationLayer.css';
import {sampleBase64pdf} from '../data/samplebase64pdf.js'

const Resume = () => {
    pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;
    
    const [numPages, setNumPages] = useState(null);
    const [pageNumber, setPageNumber] = useState(1);

    function onDocumentLoadSuccess({ numPages }) {
        setNumPages(numPages);
    }

    function changePage(offset) {
        setPageNumber(prevPageNumber => prevPageNumber + offset);
      }
    
    function previousPage() {
        changePage(-1);
        window.scroll({ top:0, behavior: 'smooth'})
    }
    
    function nextPage() {
        changePage(1);
        window.scroll({ top:0, behavior: 'smooth'})
    }

    return (
        <Container fluid className="py-5 justify-content-center align-items-center">
            {/* <Row className="justify-content-center">
            </Row> */}
            <Row className="justify-content-center text-center align-items-center mr-0">
                <Col xl={1} lg={1} md={1} sm={1} xs={1} className="text-center">
                    {/* Properly keep this column's items as equal width */}
                    <h1 className="txt-big">R</h1>
                    <h1 className="txt-big">e</h1>
                    <h1 className="txt-big">s</h1>
                    <h1 className="txt-big">u</h1>
                    <h1 className="txt-big">m</h1>
                    <h1 className="txt-big">e</h1>
                    <Image src="/images/resume-folder.svg" style={{width : '3rem'}}/>
                </Col>
                {/* <Col xl={2} lg={2} md={2} sm={2} xs={2}> */}
                {/* </Col> */}
                <Col xl={10} lg={10} md={10} sm={10} xs={10} style={{ overflow : 'hidden' }}>
                    <Document
                        file={sampleBase64pdf}
                        onLoadSuccess={onDocumentLoadSuccess}
                    >
                        <Page pageNumber={pageNumber} 
                            className="pdf-page-size"        
                        />
                    </Document>
                    {numPages &&
                        <Row className="justify-content-center text-center align-items-center m-3">
                            <Button className="btn-light" onClick={previousPage} disabled={pageNumber <= 1}><i className="fas fa-chevron-left fa-2x"></i></Button>
                            <h5>{pageNumber} of {numPages}</h5>
                            <Button className="btn-light" onClick={nextPage} disabled={pageNumber >= numPages}><i className="fas fa-chevron-right fa-2x"></i></Button>
                        </Row>
                    }
                    <Row className="justify-content-center m-3">
                        <Button className="custom-colors bg-info" download={`MohitMakhija-Resume`} href={sampleBase64pdf} title='' ><i className="fas fa-download"></i> Download My Resume</Button>
                    </Row>
                </Col>
            </Row>
        </Container>
    )
}

export default Resume
