import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import projects from '../data/projects'
import generalInfo from '../data/general'

const Footer = () => {
    var currentYear = new Date().getFullYear()
    return (
        <Container fluid id="footer" className="jumbotron m-0 pb-2">
            <Row>
                <Col id="contact-info" className="mb-4" xl={4} lg={4} md={6} sm={6} xs={12} data-aos="fade-up" data-aos-duration="200" data-aos-anchor="#contact-info" data-aos-once="true">
                    <h2>Contact Information</h2>
                    <p>{generalInfo.contactTagLine}</p>
                    <p><i className="fas fa-envelope"></i> <a href={`mailto:${generalInfo.email}`}>{generalInfo.email}</a></p>
                    <p><i className="fab fa-linkedin"></i> <a href={generalInfo.linkedin} target="_blank" rel="noreferrer">LinkedIn</a></p>
                </Col>
                <Col id="latest-work" className="mb-4" xl={2} lg={2} md={6} sm={6} xs={12} data-aos="fade-up" data-aos-duration="200" data-aos-delay="150" data-aos-anchor="#latest-work" data-aos-once="true">
                    <h2>Latest Work</h2>
                    <ul style={{ listStyleType : 'none' }} className="p-0">
                        {[0,1,2,3].map(i => (
                            projects[i] && 
                            <li className="m-1"><a>{projects[i].name}</a></li>
                        )
                        )}
                        <li className="m-1"><a href='#projects'>All Projects</a></li>
                    </ul>
                </Col>
                <Col id="curr-obj" className="mb-4" xl={3} lg={3} md={6} sm={6} xs={12} data-aos="fade-up" data-aos-duration="200" data-aos-delay="300" data-aos-anchor="#curr-obj" data-aos-once="true">
                    <h2>Current Objective</h2>
                    <p>{generalInfo.currentObjective}</p>
                </Col>
                <Col id="find-me-on" className="mb-4" xl={3} lg={3} md={6} sm={6} xs={12} data-aos="fade-up" data-aos-duration="200" data-aos-delay="450" data-aos-anchor="#find-me-on" data-aos-once="true">
                    <h2>Find Me On</h2>
                    <a href={generalInfo.linkedin} target="_blank" rel="noreferrer"><i className="fab fa-linkedin fa-2x m-1"></i></a>
                    <a href={generalInfo.github} target="_blank" rel="noreferrer"><i className="fab fa-github fa-2x m-1"></i></a>
                    <a href={generalInfo.facebook} target="_blank" rel="noreferrer"><i className="fab fa-facebook fa-2x m-1"></i></a>
                </Col>
            </Row>
            <h5 className="text-center">Mohit Makhija <span>&copy;</span> {currentYear}</h5>
            {/* <p className="text-center">Made with <i className="fas fa-heart"></i> as a human.</p> */}
        </Container>
    )
}

export default Footer
