import React, { useEffect, useState } from 'react'
import { Container } from 'react-bootstrap';
import Header from './components/Header'
import Footer from './components/Footer'
import generalInfo from './data/general'
import HomeScreen from './screens/HomeScreen'
import ThemeContext from './components/ThemeContext'
import AOS from 'aos'
import 'aos/dist/aos.css'


function App() {
  useEffect(() => {
    AOS.init({
      // offset: 200,
      duration: 600,
      easing: 'ease-in-sine',
      // once : true,
      // delay: 100,
    });
  }, [])
  const storedTheme = localStorage.getItem("theme")
  const [theme, setTheme] = useState(storedTheme ? storedTheme : 'light');
  localStorage.setItem("theme", theme)
  const toggleTheme = () => {
    theme === 'light' ? setTheme('dark') : setTheme('light')
    localStorage.setItem("theme", theme)
  }
  const themeSettings = {
    theme : theme,
    toggleTheme
  }
  console.log("In App : ",ThemeContext)
  const scrolltop = () => {
    window.scroll({top: 0})
  }
  return (
    <ThemeContext.Provider value={themeSettings}>
    <main className={theme === 'light' ? 'liteMode' : 'darkMode'}>
          <Container fluid className="portfolio-content m-0 p-0" id="portfolio-content">

            <Header name={generalInfo.name} social={[generalInfo.linkedin, generalInfo.github, generalInfo.facebook]} />
            <Container fluid className="m-0 p-0 pt-5 className={theme === 'light' ? 'liteMode' : 'darkMode'}">
              <HomeScreen />
            </Container>
            <i data-aos="fade-in" data-aos-offset="100" onClick={scrolltop} style={{position : 'fixed', bottom : '20px', right : '20px', color : 'lightgray'}} className="fas fa-arrow-circle-up fa-3x"></i>
            <Footer />

          </Container>
      </main>
    </ThemeContext.Provider>
  );
}

export default App;
