import React from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';

const Education = () => {
    return (
    <Container id="education" fluid className="pt-60 my-3 px-4">
        <h1>Education</h1>
        <Row>
            <Col>
                {/* <Card> */}
                    <div>
                        <VerticalTimeline>
                            <VerticalTimelineElement
                                className="vertical-timeline-element--work justify-content-center align-items-center"
                                contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                                contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                                date="2016 - 2020"
                                iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff', padding : '5px' }}
                                icon={<i className="fas fa-university fa-2x"></i>}
                                >
                                {/* <h3 className="vertical-timeline-element-title">Web Developer</h3> */}
                                <h4 className="vertical-timeline-element-subtitle">B.E., Computer Engineering</h4>
                                {/* <p>2016 - 2020</p> */}
                                <p>CGPI - 8.14</p>
                                <p>Vivekanand Education Society's Institute of Technology, Mumbai</p>
                            </VerticalTimelineElement>
                            <VerticalTimelineElement
                                className="vertical-timeline-element--work justify-content-center align-items-center"
                                contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                                contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                                date="2016"
                                iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff', padding : '5px' }}
                                icon={<i className="fas fa-school fa-2x"></i>}
                                >
                                {/* <h3 className="vertical-timeline-element-title">Web Developer</h3> */}
                                <h4 className="vertical-timeline-element-subtitle">HSC (12th grade), Higher Secondary Certificate in Science</h4>
                                {/* <p>2015 - 2016</p> */}
                                <p>Percentage - 68%</p>
                                <p>Smt. Chandibai Himathmal Mansukhani College</p>
                            </VerticalTimelineElement>
                            <VerticalTimelineElement
                                className="vertical-timeline-element--work justify-content-center align-items-center"
                                contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                                contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                                date="2014"
                                iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff', padding : '5px' }}
                                icon={<i className="fas fa-school fa-2x"></i>}
                                >
                                {/* <h3 className="vertical-timeline-element-title">Web Developer</h3> */}
                                <h4 className="vertical-timeline-element-subtitle">SSC (10th grade)</h4>
                                {/* <p>2013 - 2014</p> */}
                                <p>Percentage - 86.4%</p>
                                <p>Jhulelal Trust School</p>
                            </VerticalTimelineElement>
                        </VerticalTimeline>
                    </div>
                {/* </Card> */}
            </Col>
        </Row>
    </Container>

    )
}

export default Education
