import React, { useState } from 'react'
import { Button, Container, Row, Col, Image, OverlayTrigger, Popover, Modal, Card } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import generalInfo from '../data/general'
import highlightImage from '../welcomeImage-min2.png'
import Resume from './Resume'

const Highlight = () => {
    const [resumeShow, setResumeShow] = useState(false)

    return (
    <>
      <Container fluid className="pt-4 px-4 mb-5">
        <Row className="justify-content-center align-items-center text-center">
          <Col xl={5} lg={5} md={6} sm={9} xs={12} className="mb-3">
            <Image id="myImage" className="myImage" data-aos="zoom-in" data-aos-anchor-placement="#top" src={highlightImage} alt="My Image" fluid />
          </Col>
          <Col xl={6} lg={6} md={6} sm={12} xs={12} className="my-3">
            <h4 data-aos="zoom-in" data-aos-anchor="#top" data-aos-delay="600" className="hi txt-no-transform txt-medium letter-spacing-medium">Hi, I'm</h4>
            <h2 data-aos="zoom-in" data-aos-anchor="#top" data-aos-delay="1100" className="name txt-big playfair-display">{generalInfo.name}</h2>
            <h4 data-aos="zoom-in" data-aos-anchor="#top" data-aos-delay="1600" className="title mb-4">{generalInfo.title}</h4>
            <p data-aos="fade-up" data-aos-anchor="#top" data-aos-delay="2100" className="buttons">
              <a href='#projects'>
                  <Button variant="primary m-1">View Projects</Button>
              </a>

              {/* <a href='/resume'> */}
                  <Button variant="info m-1" onClick={() => setResumeShow(true)}>My Resume</Button>
              {/* </a> */}

              <Modal
                size="xl"
                show={resumeShow}
                onHide={() => setResumeShow(false)}
                aria-labelledby="example-modal-sizes-title-lg"
              >
                <Modal.Header closeButton>
                  <Modal.Title id="example-modal-sizes-title-lg">
                    My Resume
                  </Modal.Title>
                </Modal.Header>
                <Modal.Body className="p-2">
                  <Resume />
                </Modal.Body>
                <Modal.Footer className="p-0">
                  <Button className="btn-block" onClick={() => setResumeShow(false)}>Close</Button>
                </Modal.Footer>
              </Modal>

              <OverlayTrigger
                trigger="hover"
                key="top"
                placement="top"
                overlay={
                  <Popover id={`popover-positioned-top`}>
                    {/* <Popover.Title as="h3">{`Popover top`}</Popover.Title> */}
                    <Popover.Content className="bg-secondary">
                    <p className="lato font-weight-bold" style={{fontSize : '1rem'}}>{generalInfo.currentObjective}</p>
                    </Popover.Content>
                  </Popover>
                }
              >
                <Button variant="secondary m-1">My current Objective <i className="fas fa-mouse-pointer"></i></Button>
              </OverlayTrigger>
            </p>
          </Col>
        </Row>

      </Container>

      <Container id="a-l-a-m" fluid className="py-5 my-5" data-aos="fade-up" data-aos-duration="300" data-aos-anchor="#a-l-a-m">
          {/* <Row> */}
              {/* <Col> */}
                  <Card>
                      <Card.Body>
                          <Card.Title> <b><h5> a little about me... </h5></b></Card.Title>
                          <Card.Text>
                              <p className="txt-small copernicus-book-font">I am a fresher as a Bachelor in Computer Engineering, 2020 from V.E.S.I.T, Mumbai. I have experience in web development with different technologies.  In general, I like to code and make things along with taking part in new ideas or design thoughts.</p>
                          </Card.Text>
                      </Card.Body>
                  </Card>
              {/* </Col> */}
          {/* </Row> */}
      </Container>

      <Container fluid id="tech-stack" className="my-4 px-4 text-center">
          <h6>I have worked with <i className="fas fa-arrow-down"></i></h6>
          <Row className="justify-content-center align-items-center bg-white">
          {generalInfo.techStack.length !== 0 && 
          generalInfo.techStack.map((techIcon, index) => (
              <Image key={techIcon} src={techIcon} className="m-2" style={{width : '45px', height : '45px'}} data-aos="zoom-in" data-aos-duration="100" data-aos-anchor="#tech-stack" data-aos-delay={index*100}/>
          ))
          }
          </Row>
          <h6 className="mt-2">I never limit myself from learning new things.</h6>
      </Container>

    </>
    )
}

export default Highlight
