import React from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';

const Experience = () => {
    return (
        <Container id="experience" fluid className="pb-5 my-3 px-4 jumbotron">
            <h1>Experience</h1>
            <Row>
                <Col>
                    {/* <Card> */}
                        <div>
                            <h3 className="txt-no-transform playfair-display">Web Developer</h3>
                            <h4 className="txt-no-transform">Freelancer</h4>
                            <VerticalTimeline layout="1-column-left">
                                <VerticalTimelineElement
                                    className="vertical-timeline-element--work justify-content-center align-items-center"
                                    contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                                    contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                                    date="10/20 - 11/20"
                                    iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff', padding : '5px' }}
                                    icon={<i className="fas fa-shopping-cart fa-2x"></i>}
                                    >
                                    {/* <h3 className="vertical-timeline-element-title">Web Developer</h3> */}
                                    <h4 className="vertical-timeline-element-subtitle">I.K.I - Online Store</h4>
                                    <p>Stack : MongoDB(mongoose), ExpressJS, ReactJS, NodeJS </p>
                                    <p>Roles : UI Design, Architecture Design, Coding, Deployment</p>
                                </VerticalTimelineElement>
                                <VerticalTimelineElement
                                    className="vertical-timeline-element--work justify-content-center align-items-center"
                                    contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                                    contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                                    date="09/20 - 10/20"
                                    iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff', padding : '5px' }}
                                    icon={<i className="fas fa-utensils fa-2x"></i>}
                                    >
                                    {/* <h3 className="vertical-timeline-element-title">Web Developer</h3> */}
                                    <h4 className="vertical-timeline-element-subtitle">Royal Tiffin</h4>
                                    <p>Stack : HTML, CSS, Wordpress</p>
                                    <p>Roles : Frontend, Wordpress Admin Backend</p>
                                </VerticalTimelineElement>
                            </VerticalTimeline>
                        </div>
                    {/* </Card> */}
                </Col>
            </Row>
            <Row>
                <Col>
                    {/* <Card> */}
                        <div>
                            <h3 className="txt-no-transform playfair-display">Web Developer</h3>
                            <h4 className="txt-no-transform">Internship - VESIT Renaissance Cell</h4>
                            <p className="lato">Stack : Laravel, MySQL, HTML, CSS, Bootstrap, JQuery</p>
                            <VerticalTimeline layout="1-column-left">
                                <VerticalTimelineElement
                                    className="vertical-timeline-element--work justify-content-center align-items-center"
                                    contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                                    contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                                    date="12/18 - 01/19"
                                    iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff', padding : '5px' }}
                                    icon={<i className="fas fa-id-card fa-2x"></i>}
                                    >
                                    {/* <h3 className="vertical-timeline-element-title">Web Developer</h3> */}
                                    <h4 className="vertical-timeline-element-subtitle">ID Card printing module</h4>
                                    <p>Roles : Team Leader, Frontend, Backend, UI Prototyping</p>
                                </VerticalTimelineElement>
                                <VerticalTimelineElement
                                    className="vertical-timeline-element--work justify-content-center align-items-center"
                                    contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                                    contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                                    date="06/18 - 07/18"
                                    iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff', padding : '5px' }}
                                    icon={<i className="fas fa-users fa-2x"></i>}
                                    >
                                    {/* <h3 className="vertical-timeline-element-title">Web Developer</h3> */}
                                    <h4 className="vertical-timeline-element-subtitle">Student Enrollment for Exam Department</h4>
                                    <p>Roles : Frontend, Backend, UI Prototyping</p>
                                </VerticalTimelineElement>
                            </VerticalTimeline>
                        </div>
                    {/* </Card> */}
                </Col>
            </Row>
        </Container>
    )
}

export default Experience
